<template>
  <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
    <FormItem label="" prop="name">
      <Input v-model="formValidate.name"  placeholder="" class="formPublic"></Input>
      <div style="clear: both"></div>
    </FormItem>
    <FormItem label="" prop="mail">
      <Input v-model="formValidate.mail" placeholder=""  class="formPublic"></Input>
      <div style="clear: both"></div>
    </FormItem>
    <FormItem label="" prop="newPassword">
      <Input v-model="formValidate.newPassword" placeholder=""  class="formPublic"></Input>
      <div style="clear: both"></div>
    </FormItem>
    <FormItem label="" prop="title">
      <Input v-model="formValidate.title" placeholder=""  class="formPublic"></Input>
      <div style="clear: both"></div>
    </FormItem>
    <FormItem label="" prop="sender">
      <Input v-model="formValidate.sender" placeholder=""  class="formPublic"></Input>
      <div style="clear: both"></div>
    </FormItem>
    <FormItem>
      <Button type="primary" @click="handleSubmit('formValidate')" style="float:left"></Button>
    </FormItem>
  </Form>
</template>

<script>
    export default {
      data () {
        return {
          formValidate: {
            name: '',
            mail: '',
            newPassword: '',
            title: '',
            sender: ''
          },
          ruleValidate: {
            name: [
              { required: true, message: '', trigger: 'blur' }
            ],
            mail: [
              { required: true, message: '', trigger: 'blur' },
              { type: 'email', message: '', trigger: 'blur' }
            ],
            newPassword: [
              { required: true, message: '', trigger: 'blur' }
            ],
            title: [
              { required: true, message: '', trigger: 'blur' }
            ],
            sender: [
              { required: true, message: '', trigger: 'blur' }
            ]
          }
        }
      },
      methods: {
        handleSubmit (name) {
          this.$refs[name].validate((valid) => {
            if (valid) {
              this.$Message.success('Success!')
            } else {
              this.$Message.error('Fail!')
            }
          })
        }
      }
    }
</script>
<style>
.formPublic{
  width:450px;
  float:left;
}
.ivu-form-item-content{
  margin-bottom:10px;
}
</style>



