<template>
  <div>
    <Row style="border-bottom: 2px solid #41b883;padding-bottom: 16px;margin-bottom: 26px;">
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;" @click="addUser()"></Button>
      </div>
      </Col>
      <Col span="12">
      <div>
        <Input placeholder="..." style="width: 500px"></Input>
      </div>
      </Col>
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;"></Button>
      </div>
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <div  style="margin-top:20px;">
        <Table border :columns="columns1" :data="data1"></Table>
      </div>
      </Col>
    </Row>
    <Modal
      v-model="modal1"
      title=" > "
      width="600"
      :styles="{top: '20px'}"
      @on-ok="ok"
      @on-cancel="cancel">
      <Form :label-width="80" :rules="ruleValidate" ref="formValidate" :model="formValidate">
        <FormItem label="" prop="userName">
          <Input type="text" v-model="formValidate.userName"  placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="name">
          <Input type="text" v-model="formValidate.name" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="password1">
          <Input type="password" v-model="formValidate.password1" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="password2">
          <Input type="password" v-model="formValidate.password2" placeholder=""></Input>
        </FormItem>
        <FormItem label="" v-model="formValidate.mail">
          <Input type="text" v-model="formValidate.mail" placeholder=""></Input>
        </FormItem>
        <FormItem label="" v-model="formValidate.tel">
          <Input type="text" v-model="formValidate.tel" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="roles">
          <RadioGroup v-model="formValidate.role">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
      </Form>
    </Modal>
    <Modal
      v-model="modal2"
      title=" > "
      width="600"
      :styles="{top: '20px'}"
      @on-ok="ok"
      @on-cancel="cancel">
      <Form :label-width="80" :rules="ruleValidate" ref="formValidate" :model="formValidate">
        <FormItem label="" prop="userName">
          <Input type="text" v-model="formValidate.userName" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="name">
          <Input type="text" v-model="formValidate.name" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="password1">
          <Input type="password" v-model="formValidate.password1" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="password2">
          <Input type="password" v-model="formValidate.password2" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formValidate.email" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formValidate.tel" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="roles">
          <RadioGroup v-model="formValidate.role">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
  export default {
    name: 'users',
    components: {
    },
    data () {
      return {
        modal1: false,
        modal2: false,
        formValidate: {
          userName: '',
          name: '',
          password1: '',
          password2: '',
          email: '',
          tel: '',
          role: ''
        },
        ruleValidate: {
          userName: [
            { required: true, message: '', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          password1: [
            { required: true, message: '', trigger: 'blur' }
          ],
          password2: [
            { required: true, message: '', trigger: 'blur' }
          ],
          mail: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'email', message: '', trigger: 'blur' }
          ],
          roles: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        columns1: [
          {
            title: '',
            key: 'userName'
          },
//          {
//            title: '',
//            key: 'name'
//          },
          {
            title: '',
            key: 'phone'
          },
          {
            title: '',
            key: 'email'
          },
          {
            title: '',
            key: 'option',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.editUser(params)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '',
                        content: '<p></p>',
                        onOk: () => {
                          this.$Message.info('')
                        },
                        onCancel: () => {
                          this.$Message.info('')
                        }
                      })
                    }
                  }
                }, '')
              ])
            }
          }
        ],
        data1: [
//          {
//            userName: 'admin',
//            name: '',
//            phone: '185xxxxxxxx',
//            email: 'xxx@.changhong.com'
//          },
//          {
//            userName: 'user1',
//            name: '',
//            phone: '155xxxxxxxx',
//            email: 'xxx@.changhong.com'
//          },
//          {
//            userName: 'user2',
//            name: '',
//            phone: '177xxxxxxxx',
//            email: 'xxx@.changhong.com'
//          }
        ]
      }
    },
    created: function () {
//      this.$http.get('/api/shiro/api/user/list').then(function (data) {
//        console.log(data)
//        this.data = data.body.data
//      }).catch(function (response) {
//        console.log(response)
//      })
    },
    methods: {
      addUser () {
        this.formValidate = {
          userName: '',
          name: '',
          password1: '',
          password2: '',
          email: '',
          tel: '',
          role: ''
        }
        this.modal1 = true
      },
      editUser (params) {
        console.log(params.row)
        this.modal2 = true
        this.formValidate = {
          userName: params.row.userName,
          name: params.row.name,
          password1: '123456',
          password2: '123456',
          email: params.row.email,
          tel: params.row.tel,
          role: ''
        }
      },
      ok () {
        this.$Message.info('')
      },
      cancel () {
        this.$Message.info('')
      }
    }
  }
</script>
<style>
  .addBtn{
    width: 133px;
    height: 34px;
    background-color: rgba(0, 204, 102, 1);
    border-width: 1px;
    border-style: solid;
    border-color: rgba(102, 255, 0, 1);
    border-radius: 3px;
    box-shadow: none;
    color: #fff;
    cursor: pointer;
  }
</style>



