import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import homepage from '@/pages/homepage'
import leaknav from '@/pages/leaknav'
import allleak from '@/pages/leak/allleak'
import examineleak from '@/pages/leak/examineleak'
import handleleak from '@/pages/leak/handleleak'
import submitleak from '@/pages/leak/submitleak'
import verificationleak from '@/pages/leak/verificationleak'
import workordernav from '@/pages/workordernav'
import examineworkorder from '@/pages/workorder/examineworkorder'
import allworkorder from '@/pages/workorder/allworkorder'
import handleworkorder from '@/pages/workorder/handleworkorder'
import verificationworkorder from '@/pages/workorder/verificationworkorder'
import systemnav from '@/pages/systemnav'
import presentation from '@/pages/presentation'
import users from '@/pages/system/users'
import roles from '@/pages/system/roles'
import log from '@/pages/system/log'
import mail from '@/pages/system/mail'
import dict from '@/pages/system/dict'
import label from '@/pages/system/label'
import Login from '@/pages/Login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          path: '/homepage',
          icon: 'fa fa-home',
          name: '',
          component: homepage
        },
        {
          path: '/leaknav',
          name: '',
          icon: 'fa fa-bug',
          component: leaknav,
          children: [
            {
              path: '/leaknav/leak-1',
              name: '',
              component: allleak
            },
            {
              path: '/leaknav/leak-2',
              name: '',
              component: submitleak
            },
            {
              path: '/leaknav/leak-3',
              name: '',
              component: examineleak
            },
            {
              path: '/leaknav/leak-4',
              name: '',
              component: handleleak
            },
            {
              path: '/leaknav/leak-5',
              name: '',
              component: verificationleak
            }
          ]
        },
        {
          path: '/workordernav',
          name: '',
          icon: 'fa fa-clipboard',
          component: workordernav,
          children: [
            {
              path: '/workordernav/workorder-1',
              name: '',
              component: allworkorder
            },
            {
              path: '/workordernav/workorder-2',
              name: '',
              component: handleworkorder
            },
            {
              path: '/workordernav/workorder-3',
              name: '',
              component: examineworkorder
            },
            {
              path: '/workordernav/workorder-4',
              name: '',
              component: verificationworkorder
            }
          ]
        },
        {
          path: '/systemnav',
          name: '',
          icon: 'fa fa-cogs',
          component: systemnav,
          children: [
            {
              path: '/systemnav/system-1',
              name: '',
              component: users
            },
            {
              path: '/systemnav/system-2',
              name: '',
              component: roles
            },
            {
              path: '/systemnav/system-3',
              name: '',
              component: dict
            },
            {
              path: '/systemnav/system-4',
              name: '',
              component: label
            },
            {
              path: '/systemnav/system-5',
              name: '',
              component: mail
            },
            {
              path: '/systemnav/system-6',
              name: '',
              component: log
            }
          ]
        },
        {
          path: '/presentation',
          name: '',
          icon: 'fa fa-file-word-o',
          component: presentation
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      components: {
        blank: Login
      }
    }
  ]
})



