<template>
  <div>
    <Row style="border-bottom: 2px solid #41b883;padding-bottom: 16px;margin-bottom: 26px;">
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;" @click="addDict()"></Button>
      </div>
      </Col>
      <Col span="12">
      <div>
        <span></span>
        <Select v-model="model1" style="width:120px">
          <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        <span style="margin-left: 40px;"></span><Input placeholder="..." style="width: 300px"></Input>
      </div>
      </Col>
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;"></Button>
      </div>
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <div style="margin-top:20px;">
        <Table border :columns="columns0" :data="data0"></Table>
      </div>
      </Col>
    </Row>
    <Modal
      v-model="modal0"
      title=""
      @on-ok="ok"
      @on-cancel="cancel"
      :styles="{top: '20px'}">
      <Form :label-width="80" ref="formValidate" :model="formDict">
        <FormItem label="">
          <Input type="text" v-model="formDict.key"  placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formDict.tag" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formDict.type" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formDict.describe" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formDict.order" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="textarea" v-model="formDict.mark" placeholder=""></Input>
        </FormItem>
      </Form>
    </Modal>
    <Modal
      v-model="modal1"
      title=""
      @on-ok="ok"
      @on-cancel="cancel"
      :styles="{top: '20px'}">
      <Form :label-width="80" ref="formValidate" :model="formDict">
        <FormItem label="">
          <Input type="text" v-model="formDict.key"  placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formDict.tag" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formDict.type" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formDict.describe" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="text" v-model="formDict.order" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Input type="textarea" v-model="formDict.mark" placeholder=""></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
  export default {
    name: 'Dict',
    components: {
    },
    methods: {
      addDict () {
        this.formDict = {}
        this.modal0 = true
      },
      ok () {
      },
      cancel () {
      }
    },
    data () {
      return {
        modal0: false,
        modal1: false,
        checkedNodes: '',
        userid: '',
        username: '',
        columns0: [
          {
            title: '',
            key: 'key'
          },
          {
            title: '',
            key: 'tag'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'describe'
          },
          {
            title: '',
            key: 'order'
          },
          {
            title: '',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.modal1 = true
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.modal1 = true
                      console.log(params.row)
                      this.formDict = {
                        key: params.row.key,
                        tag: params.row.tag,
                        type: params.row.type,
                        describe: params.row.describe,
                        order: params.row.order,
                        mark: ''
                      }
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '',
                        content: '<p></p><p></p>',
                        onOk: () => {
                          this.$Message.info('')
                        },
                        onCancel: () => {
                          this.$Message.info('')
                        }
                      })
                    }
                  }
                }, '')
              ])
            }
          }
        ],
        data0: [
          {
            key: '11',
            tag: '',
            type: 'attack_type',
            describe: '',
            order: '1'
          },
          {
            key: '12',
            tag: 'http',
            type: 'sys_operation',
            describe: 'http',
            order: '2'
          },
          {
            key: '13',
            tag: '',
            type: 'error_code',
            describe: '',
            order: '3'
          }
        ],
        formDict: {
          key: '',
          tag: '',
          type: '',
          describe: '',
          order: '',
          mark: ''
        },
        model1: 'all',
        typeList: [
          {
            value: 'all',
            label: ''
          },
          {
            value: 'attack_type',
            label: 'attack_type'
          },
          {
            value: 'error_code',
            label: 'error_code'
          },
          {
            value: 'sys_operation',
            label: 'sys_operation'
          }
        ]
      }
    }
  }
</script>



