<template>
  <div>
    <Row style="border-bottom: 2px solid #41b883;padding-bottom: 16px;margin-bottom: 26px;">
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;" @click="addTag()"></Button>
      </div>
      </Col>
      <Col span="12">
      <div>
        <Input placeholder="" style="width: 500px"></Input>
      </div>
      </Col>
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;"></Button>
      </div>
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <div  style="margin-top:20px;">
        <Table border :columns="columns1" :data="data1"></Table>
      </div>
      </Col>
    </Row>

    <Modal
      v-model="modal1"
      title=""
      width="600"
      :styles="{top: '20px'}"
      @on-ok="ok"
      @on-cancel="cancel">
      <Form :label-width="80" ref="formValidate" :model="formLable">
        <FormItem label="">
          <Input type="text" v-model="formLable.tagName"  placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Transfer
            :data="data"
            :target-keys="targetKeys"
            :render-format="render"
            @on-change="onChange"></Transfer>
        </FormItem>
        <FormItem label="">
          <Input type="textarea" v-model="formLable.mark" placeholder=""></Input>
        </FormItem>
      </Form>
    </Modal>
    <Modal
      v-model="modal2"
      title=""
      width="600"
      :styles="{top: '20px'}"
      @on-ok="ok"
      @on-cancel="cancel">
      <Form :label-width="80" ref="formValidate" :model="formLable">
        <FormItem label="">
          <Input type="text" v-model="formLable.tagName"  placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Transfer
            :data="data"
            :target-keys="targetKeys2"
            :render-format="render"
            @on-change="onChange"></Transfer>
        </FormItem>
        <FormItem label="">
          <Input type="textarea" v-model="formLable.mark" placeholder=""></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
  export default {
    name: 'Label',
    components: {
    },
    data () {
      return {
        modal1: false,
        modal2: false,
        data: [
          { 'key': '1', 'label': 'user1' },
          { 'key': '2', 'label': 'user2' },
          { 'key': '3', 'label': 'user3' },
          { 'key': '4', 'label': 'user4' },
          { 'key': '5', 'label': 'user5' },
          { 'key': '6', 'label': 'user6' },
          { 'key': '7', 'label': 'user7' },
          { 'key': '8', 'label': 'user8' },
          { 'key': '9', 'label': 'user9' }
        ],
        targetKeys: [],
        targetKeys2: [],
        formLable: {
          tagName: '',
          mark: ''
        },
        columns1: [
          {
            title: '',
            key: 'tagName'
          },
          {
            title: '',
            key: 'users'
          },
          {
            title: '',
            key: 'time'
          },
          {
            title: '',
            key: 'option',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.editUser(params)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '',
                        content: '<p></p>',
                        onOk: () => {
                          this.$Message.info('')
                        },
                        onCancel: () => {
                          this.$Message.info('')
                        }
                      })
                    }
                  }
                }, '')
              ])
            }
          }
        ],
        data1: [
          {
            tagName: '',
            users: 'user1 user2 user3',
            time: '2017/11/10 11:39:00'
          },
          {
            tagName: '',
            users: 'user4 user5 user6',
            time: '2017/11/10 12:00:10'
          },
          {
            tagName: '',
            users: 'user7 user8 user9',
            time: '2017/11/8 11:55:00'
          }
        ]
      }
    },
    methods: {
      addTag () {
        this.targetKeys = []
        this.formLable = {
          tagName: '',
          mark: ''
        }
        this.modal1 = true
      },
      editUser (params) {
        console.log(params.row)
        console.log(params.row.tagName)
        var strs = params.row.users
        var users = [] // 
        users = strs.split(' ') // 
        for (var i = 0; i < users.length; i++) {
          for (var j = 0; j < this.data.length; j++) {
            if (users[i] === this.data[j].label) {
              this.targetKeys2.push(this.data[j].key)
            }
          }
        }
        this.modal2 = true
        this.formLable = {
          tagName: params.row.tagName,
          mark: ''
        }
      },
      ok () {
        this.targetKeys2 = []
        this.$Message.info('')
      },
      cancel () {
        this.targetKeys2 = []
        this.$Message.info('')
      },
      render (item) {
        return item.label
      },
      onChange (newTargetKeys, direction, moveKeys) {
        console.log(direction)
        console.log(moveKeys)
        console.log(newTargetKeys)
        this.targetKeys = newTargetKeys
      }
    }
  }
</script>



